@import 'include-media/dist/_include-media';
@import 'bootstrap-sass/assets/stylesheets/bootstrap/_variables';

@import 'becomeTeacher';
@import 'react-big-calendar';
@import 'teacherinfo';
@import 'chat';

$breakpoints: (
  'sm': $screen-sm,
  'md': $screen-md,
  'lg': screen-lg
);

#root {
  background-color: #cde;
}

.entrance {
  .box {
    background-color: $body-bg;
    border-radius: 5px;
    box-shadow: 4px 4px 10px black;
    padding: 2rem;
    .logo {
      font: bold 2rem serif;
      padding: 1rem;
    }
    .social-auth-links {
      margin-top: 1rem;
    }
  }
}

.vertical-center {
  display: flex;
  align-items: center;
}

.profile.edit.dropzone  {
  position: relative;
  padding: 2rem;
  button {
    text-transform: uppercase;
  }
  form {
    margin-top: 2rem;
    button.save {
        margin-top: 1rem;
    }
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.drop-hovered {
  .overlay {
    border-radius: 25px 50px;
    border: 3px dashed rgba(0, 0, 0, 0.6);
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1000;
  }
}

#root > div > div {
  min-height: 100vh;
}

.main-page {
  display: flex;
  flex-direction: column;
  > .content {
    flex: 1 0 auto;
  }
  > footer {
    flex: 0 0 auto;
    width: 100%;
    padding: 1rem;
    ul {
      text-align: center;
      li {
        display: inline-block;
      }
    }
  }
}

#adminChat {

  ul.chat-room-control {
    display: flex;
    justify-content: center;
    margin: 0;
    list-style-type: none;
    li {
      button {
        width: 10rem;
        margin-left: 3rem;
      }
    }
  }
}

#clock {
  text-align: center;
  color: #556;
}

#classRoom {
  .lessons > .panel {
    min-height: 80vh;
    h4 {
      font-size: 3rem;
      margin-top: 0;
    }
  }
}



